@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300&display=swap');

.mt-cart {
	color: rgb(25, 25, 27);
	background-image: linear-gradient(90deg, #131212, rgba(17, 17, 17, 0.2), #353536);
	padding: 32px;
	display: flex;
	position: right;
	flex-direction: column;
	justify-content: flex-start;
	width: 400px;
}
.cartUdpdated{
	font-family: 'Chakra Petch' ;
	font-weight: bold;
}
h2{
	font-family: 'Chakra Petch' ;
}
h3{
	font-family: 'Chakra Petch' ;
}

.mt-cart-add-button {
	margin-left: 32px;
	cursor: pointer;
}
.emptyCart{
	margin: 15px 0 0 0;
	font-family: 'Chakra Petch';
	font-weight:600;
}
.mt-cart-toggle-button {
	background: none;
	outline: none;
	border: none;
	font-weight: 600;
	cursor: pointer;
	align-self: self-start;
	padding: 0;
	color: #aaa9af;
	align-self: flex-end;
	padding: 6px;
	font-family: 'Chakra Petch';
	font-weight:600;
}

.mt-cart-closed {
	background-image: linear-gradient(90deg, #131212, rgba(17, 17, 17, 0.2), #353536);
	padding: 32px;
	width: 400px;
}

.mt-cart-closed > button {
	color: #aaa9af;
}

.mt-cart-toggle-button{
	color: #aaa9af !important;
	border-radius: 5px;
	background-color: rgb(25, 25, 27) !important;
	border: solid 2px #aaa9af;
	padding: 6px;
	font-family: 'Chakra Petch';
	font-weight:600;
	cursor: pointer;
}
.mt-cart-toggle-button:hover{
	color: rgb(25, 25, 27) !important;
	border-radius: 5px;
	background-color: #aaa9af !important;
	border: solid 2px rgb(25, 25, 27);
	padding: 6px;
	font-family: 'Chakra Petch';
	font-weight:600;
}
.mt-cart-toggle-button-open{
	color: #aaa9af !important;
	border-radius: 5px;
	background-color: rgb(25, 25, 27) !important;
	border: solid 2px #aaa9af ;
	padding: 6px;
	font-family: 'Chakra Petch';
	font-weight:600;
	margin: 0 0 0 165px;
}
.mt-cart-toggle-button-open:hover{
	color: rgb(25, 25, 27) !important;
	border-radius: 5px;
	background-color: #aaa9af;
	border: solid 2px rgb(25, 25, 27);
	padding: 6px;
	font-family: 'Chakra Petch';
	font-weight:600;
}
.mt-panier{
	position: absolute;
	margin: 15px 0 0 95px;
}
@media screen and (max-width: 1300px) {
	.mt-cart {
		width: 350px;
	}
	.cartUdpdated{
		font-size: 0.9em;
	}
	h2{
		font-size: 1.4em;
	}
	h3{
		font-size: 1.4em;
	}
	.mt-panier{
		width: 45px;
		height: 45px;
		margin: 10px 0 0 75px;
	}
}

@media screen and (max-width: 900px) {
	.mt-cart-toggle-button-open{
		margin-left: 125px;
	}
}
@media screen and (max-width: 520px) {
	.mt-cart {
		width: 250px;
	}
	.mt-cart-closed{
		width: 250px;
	}
	.mt-cart-toggle-button-open{
		margin-left: 55px;
		font-size: 0.7em;
	}
	.mt-cart-toggle-button{
		font-size: 0.7em;
	}
	.cartUdpdated{
		font-size: 0.6em;
		margin-left: -35px;
	}
	h2{
		font-size: 1.2em;
	}
	h3{
		font-size: 1.2em;
	}
	.mt-panier{
		width: 35px;
		height: 35px;
	}
}
@media screen and (max-width: 400px) {
	.mt-cart {
		width: 200px;
	}
	.mt-cart-closed{
		width: 200px;
	}
	.mt-cart-toggle-button-open{
		margin-left: -5px;
		font-size: 0.5em;
	}
	.mt-cart-toggle-button{
		font-size: 0.5em;
	}
	.cartUdpdated{
		font-size: 0.5em;
		margin-left: -35px;
	}
	h2{
		font-size: 1.1em;
	}
	h3{
		font-size: 1.1em;
	}
	.mt-panier{
		width: 25px;
		height: 25px;
		margin: 15px 0 0 55px;
	}
}
@media screen and (max-width: 320px) {
	
}