body{
	overflow-x: hidden;
}

.mt-banner {
	overflow-x: hidden;
	width: 1635px;
	color: black;
	text-align: right;
	padding: 32px;
	border-bottom: solid 3px #ffffff;
	display: flex;
	justify-content: flex-end;
	flex-direction: row;
	align-items: center;
	background-image: linear-gradient(45deg, #131212, #36363a);
}

.mt-title {
	text-shadow: 1px 1px 10px #f0eff5 ;
	padding-left: 62px;
	background-image: linear-gradient(45deg, #908e8e, rgba(177, 172, 172, 0.877), #f0f0f6);
	background-clip: text;
	color: transparent;
	font-size: 3em;
	margin: 0 150px 0 0;
}
.slogan{
	color: #aaa9af;
	text-shadow: 1px 1px 10px #f0eff5 ;
	margin: 0 300px 0 0;
	font-size: 1em;
}

.mt-logo {
	height: 95px;
	width: 95px;
}
.right-star{
	position: absolute;
	padding: 0 250px 0 350px;
}
.left-star{
	margin: 0 25px 0 0;
} 
@media screen and (max-width: 1300px) {
	.mt-banner{
		width: 1286px;
	}
}
@media screen and (max-width: 900px) {
	.slogan{
		color: #aaa9af;
		text-shadow: 1px 1px 10px #f0eff5 ;
		margin: 0 90px 0 -15px;
		font-size: 1em;
		text-align: center;
	}
	.right-star{
		display: none;
	}
	.left-star{
		display: none;
	} 
	.mt-banner{
		width: 836px;
	}
}
@media screen and (max-width: 770px) {
	.slogan{
		color: #aaa9af;
		text-shadow: 1px 1px 10px #f0eff5 ;
		margin: 10px 50px 0 -125px;
		font-size: 0.8em;
		text-align: center;
	}
	.mt-title {
		font-size: 2.5em;
	}
	.mt-banner{
		width: 706px;
	}
}
@media screen and (max-width: 520px) {
	.slogan{
		color: #aaa9af;
		text-shadow: 1px 1px 10px #f0eff5 ;
		margin: 10px 50px 0 -155px;
		font-size: 0.7em;
		text-align: center;
	}
	.mt-title {
		font-size: 1.5em;
		padding: 0 25px 0 25px;
	}
	.mt-logo {
		height: 65px;
		width: 65px;
	}
	.mt-banner{
		width: 456px;
	}
}
@media screen and (max-width: 400px) {
	.slogan{
		color: #aaa9af;
		text-shadow: 1px 1px 10px #f0eff5 ;
		margin: 10px 50px 0 -155px;
		font-size: 0.5em;
		text-align: center;
	}
	.mt-title {
		font-size: 1.2em;
		padding: 0 25px 0 25px;
	}
	.mt-logo {
		height: 45px;
		width: 45px;
	}
	.mt-banner{
		width: 336px;
	}
}
@media screen and (max-width: 320px) {
	.slogan{
		color: #aaa9af;
		text-shadow: 1px 1px 10px #f0eff5 ;
		margin: 10px 35px 0 -155px;
		font-size: 0.4em;
		text-align: center;
	}
	.mt-title {
		font-size: 1em;
		padding: 0 15px 0 25px;
	}
	.mt-logo {
		height: 40px;
		width: 40px;
	}
	.mt-banner {
		width: 256px;
	}
}
